import React, { Component } from 'react';
import * as filestack from 'filestack-js';
import PropTypes from 'prop-types';

class ReactFilestack extends Component {
  static defaultProps = {
    file: null,
    link: false,
    buttonText: 'Pick file',
    buttonClass: '',
    onSuccess: result => console.log(result),
    onError: error => console.error(error),
    mode: 'pick',
    options: {
      onOpen() {
        console.timeEnd();
      },
    },
    security: null,
    children: null,
    render: null,
    cname: null,
    sessionCache: false,
  };

  static propTypes = {
    file: PropTypes.objectOf(PropTypes.any),
    apikey: PropTypes.string.isRequired,
    link: PropTypes.bool,
    mode: PropTypes.string,
    buttonText: PropTypes.string,
    buttonClass: PropTypes.string,
    onSuccess: PropTypes.func,
    onError: PropTypes.func,
    options: PropTypes.objectOf(PropTypes.any),
    security: PropTypes.objectOf(PropTypes.any),
    children: PropTypes.node,
    render: PropTypes.func,
    cname: PropTypes.string,
    sessionCache: PropTypes.bool,
  };

  constructor(props) {
    super(props);
    const {
      apikey,
      security,
      cname,
      sessionCache,
      options,
    } = this.props;
    const client = filestack.init(apikey, {
      security,
      cname,
      sessionCache,
    });

    // return new Promise((resolve) => {
    //   // client.picker({ ...options, onUploadDone: resolve }).open();
    //   picker.open();
    // });

    const onFinished = (result) => {
      if (typeof onSuccess === 'function') {
        onSuccess(result);
      } else {
        console.log(result);
      }
    };

    this.state = {
      picker: client.picker({ ...options, onUploadDone: onFinished }),
    };
  }

  // componentDidMount () {
  //   const {
  //     picker,
  //   } = this.state;
  //   picker.preload();
  // }

  onClickPick = (event) => {
    console.time();
    event.stopPropagation();
    event.preventDefault();
    const {
      apikey,
      onSuccess,
      onError,
      options,
      mode,
      file,
      security,
      cname,
      sessionCache,
    } = this.props;

    const onFinished = (result) => {
      if (typeof onSuccess === 'function') {
        onSuccess(result);
      } else {
        console.log(result);
      }
    };
    const onFail = (error) => {
      if (typeof onError === 'function') {
        onError(error);
      } else {
        console.error(error);
      }
    };

    this.initClient(mode, apikey, options, file, security, cname, sessionCache)
      .then(onFinished)
      .catch(onFail);
  };

  initClient = (mode, apikey, options, file, security, cname, sessionCache) => {
    const { picker } = this.state;
    const { url, handle } = options;
    delete options.handle;
    delete options.url;
    // const client = filestack.init(apikey, {
    //   security,
    //   cname,
    //   sessionCache,
    // });

    if (mode === 'transform') {
      return new Promise((resolve, reject) => {
        try {
          resolve(picker.transform(handle, options));
        } catch (err) {
          reject(err);
        }
      });
    } else if (mode === 'retrieve') {
      return picker.retrieve(handle, options);
    } else if (mode === 'metadata') {
      return picker.metadata(handle, options);
    } else if (mode === 'storeUrl') {
      return picker.storeURL(url, options);
    } else if (mode === 'upload') {
      return picker.upload(file, options);
    } else if (mode === 'remove') {
      return picker.remove(handle, security);
    }

    return new Promise(() => {
      picker.open();
    });
  };

  render () {
    const {
      buttonClass, buttonText, link, children, render: CustomRender,
    } = this.props;
    if (CustomRender) {
      return (
        <CustomRender onPick={this.onClickPick} />
      );
    }
    const Tag = link ? 'a' : 'button';
    return (
      <Tag
        name="filestack"
        onClick={this.onClickPick}
        className={buttonClass}
      >
        {children || buttonText}
      </Tag>
    );
  }
}

export default ReactFilestack;
